import cuCustom from '@/colorui/components/cu-custom.vue'
import uniCard from "@/colorui/components/uni-card/uni-card"
export default {
	data() {
		return {
			isCard: true,
			username: '18502866256',
			password: '',
			url: '../../pages/login/login',
			baseUrl: 'http://47.104.29.236:9999/api/v1'
		}
	},
	methods: {
		regist() {
			if (!this.username) {
				uni.showToast({
					title: "账号不能为空",
					image: '../../static/toast/error.png'
				});
				return;
			}
			// 判断账号格式
			let userReg = /^[1][3,4,5,7,8][0-9]{9}$/
			if (!userReg.test(this.username)) {
				console.log(2)
				uni.showToast({
					title: "电话号码格式不正确",
					image: '../../static/toast/error.png'
				});
				return;
			}
			// 密码不能位空
			if (!this.password) {
				uni.showToast({
					title: "密码不能为空",
					image: '../../static/toast/error.png'
				});
				return;
			}
			// 判断密码长度
			if (this.password.length < 6 || this.password.length > 12) {
				uni.showToast({
					title: "密码长度为6 - 12 位",
					image: '../../static/toast/error.png'
				});
				return;
			}
			uni.showLoading({
				title: 'loading'
			});
			uni.request({
				url: this.baseUrl + '/regist',
				method: 'POST',
				header: {
					"content-type": "application/x-www-form-urlencoded"
				},
				data: {
					username: this.username,
					password: this.password
				},
				success: function(res) {
					console.log(res.data);

					if (res.data.code == '200') {
						uni.hideLoading();
						uni.showToast({
							title: res.data.msg,
						});
						uni.reLaunch({
							url: '../login/login'
						})
						return
					} else {
						uni.showToast({
							title: res.data.msg,
							image: '../../static/toast/error.png'
						});
						return
					}
				},
				fail: function(error) {
					console.log(error)
					uni.hideLoading();
					uni.showToast({
						title: '电话号码格式是否正确？',
						image: '../../static/toast/error.png'
					});
				}
			})

		}
	},
	components: {
		cuCustom,
		uniCard
	}
}
